.choice__loc__option{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 30px;
}

.loc__option__item{
    width: 350px;
    border: 1px solid #d9d9d9;
    border-radius: 5px;
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    transition: .3s ease;

    &.quiz__item__active{
        border-color: rgba(211, 64, 133, 0.5);
        transition: .3s ease;
    }

    &:hover{
        box-shadow: 0 1px 3px 0 #d9d9d9;
        transition: .3s ease;
    }

    label{
        cursor: pointer;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 15;

        input{
            position: absolute;
            right: 30px;
            top: 30px;
            width: 0;
            height: 0;
            transition: 1s ease;
            
            &:checked::after{
                position: absolute;
                content: '';
                width: 30px;
                height: 30px;
                background-color: transparent;
                border-radius: 50%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                border: 3px solid #d34085;
                box-shadow: 0 2px 6px 0 #d34085;
                transition: box-shadow .3s ease;
                transition: 1s ease;
            }
            
            &:checked::before{
                position: absolute;
                content: '';
                width: 15px;
                height: 15px;
                background-color: #d34085;
                z-index: 2;
                border-radius: 50%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                transition: 1s ease;
                
            }
        }
    }

    p.title{
        color: #363636;
        font-size: 15px;
        padding: 10px 15px;
    }
}

.loc__option__images{
    width: 348px;
    height: 219px;
    position: relative;
    z-index: 5;
    
    picture{

        source, img{
            border-radius: 5px;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

@media (max-width: 1122px){
    .choice__loc__option{
        justify-content: center;
        row-gap: 20px;
        column-gap: 20px;
    }
}

@media (max-width: 794px){
    .loc__option__item{
        p.title{
            text-align: center;
        }
    }
}

@media (max-width: 512px){
    .choice__loc__option{
        justify-content: flex-start;
    }
    .loc__option__item{
        width: calc(100% - 15px);
    }

    .loc__option__images{
        width: 100%;
    }
}