/* stylelint-disable */
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Roboto", sans-serif;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html,
body {
  height: 100%;
  line-height: 1;
  font-family: "Roboto", sans-serif;
}

img {
  user-select: none;
}

/* [class*="__container"]{

} */
.wrapper {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  overflow: hidden;
}

.btn__default {
  outline: none;
  border: none;
  border-radius: 5px;
  background-color: rgb(211, 64, 133);
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  position: relative;
  color: white;
  font-size: 16px;
}
.btn__default:after {
  content: "";
  display: block;
  width: 20px;
  height: 200px;
  margin-left: 50px;
  background: #fff;
  background: linear-gradient(90deg, hsla(0deg, 0%, 100%, 0.1) 10%, hsla(0deg, 0%, 100%, 0.2) 20%, hsla(0deg, 0%, 100%, 0.6));
  left: -40px;
  top: -100px;
  z-index: 1;
  transform: rotate(45deg);
  position: absolute;
  animation: movingFlare 2.5s ease-in-out 0.05s infinite;
}

@keyframes movingFlare {
  0% {
    left: -30px;
    margin-left: 0px;
  }
  30% {
    left: 110%;
    margin-left: 80px;
  }
  100% {
    left: 110%;
    margin-left: 80px;
  }
}
.quiz {
  width: 1200px;
  height: 650px;
  background-color: rgb(243, 243, 243);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.steps__wrapp {
  width: 1200px;
  height: 650px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 30px;
}

.steps__content {
  height: calc(100% - 104px - 61px);
  display: flex;
  padding: 20px 0;
  flex-direction: column;
  overflow: auto;
}

.steps__title {
  font-size: 26px;
  font-weight: 400;
  color: #363636;
}

.steps__footer {
  width: 100%;
  height: 104px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}
.steps__footer::after {
  content: "";
  position: absolute;
  width: calc(100% + 60px);
  height: 1px;
  background-color: #d9d9d9;
  top: 0;
  left: -30px;
}
.steps__footer .btn__prev {
  width: 43px;
  height: 43px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  outline: 0;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  cursor: pointer;
  background-color: transparent;
  margin-right: 15px;
}
.steps__footer .btn__prev svg {
  width: 18px;
  height: 18px;
  fill: rgba(0, 0, 0, 0.5);
}
.steps__footer .btn__prev:disabled {
  cursor: not-allowed;
}
.steps__footer .btn__prev:not(:disabled):hover svg {
  fill: #363636;
  transition: 0.3s linear;
}
.steps__footer .btn__next {
  display: flex;
  align-items: center;
  height: 43px;
  padding: 0 20px;
}
.steps__footer .btn__next:disabled {
  opacity: 0.8;
  cursor: not-allowed;
  box-shadow: 0 3px 10px 0 rgba(211, 64, 133, 0.5);
  animation: none;
}
.steps__footer .btn__next:disabled::after {
  display: none;
}
.steps__footer .btn__next svg {
  fill: white;
  margin-left: 5px;
  width: 18px;
  height: 18px;
}

.steps__header {
  width: 100%;
  height: 61px;
  display: flex;
  align-items: center;
  position: relative;
}
.steps__header::after {
  content: "";
  position: absolute;
  width: calc(100% + 60px);
  height: 1px;
  background-color: #d9d9d9;
  bottom: 0;
  left: -30px;
}
.steps__header p.title {
  font-weight: 400;
  color: rgba(54, 54, 54, 0.7);
  font-size: 14px;
  margin-left: 10px;
}
.steps__header svg {
  width: 20px;
  height: 20px;
  fill: #d34085;
}

@media (max-width: 1280px) {
  .quiz {
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    transform: translate(0, 0);
    position: fixed;
  }
  .steps__wrapp {
    width: 100%;
    height: 100vh;
  }
}
#step0, #step1, #step2, #step3, #step4, #step5, #step6, #step7, #step8, #step9, #step10, #other_steps, #last_step {
  display: none;
}

#step0 {
  display: block;
}

.first__step {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.first__step__content {
  width: 500px;
  padding: 0 30px;
}
.first__step__content p.header {
  font-size: 26px;
  font-weight: 700;
  color: #363636;
  position: relative;
  line-height: 140%;
}
.first__step__content p.header::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 3px;
  background-color: #363636;
  top: -20px;
  left: 0;
}
.first__step__content p.text {
  font-weight: 400;
  margin-top: 15px;
  color: rgba(54, 54, 54, 0.7);
  font-size: 16px;
}
.first__step__content .btn__default {
  width: 155px;
  height: 55px;
  font-weight: 500;
  margin-top: 30px;
}

.first__step__images {
  width: 700px;
  height: 650px;
}
.first__step__images picture source, .first__step__images picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 1280px) {
  .first__step__images {
    width: 55%;
    height: 100vh;
  }
  .first__step__content {
    width: 45%;
  }
}
@media (max-width: 990px) {
  .first__step__images {
    width: 100%;
    height: 400px;
    order: 1;
    overflow: hidden;
  }
  .first__step__images picture source, .first__step__images picture img {
    width: 100%;
    height: auto;
    object-fit: 100%;
  }
  .first__step__content {
    width: auto;
    margin-top: 60px;
    order: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 30px;
  }
  .first__step__content p.header {
    width: 500px;
    text-align: center;
  }
  .first__step__content p.header::after {
    left: 50%;
    transform: translateX(-50%);
  }
  .first__step__content p.text {
    text-align: center;
  }
  .first__step {
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    overflow: auto;
    padding-bottom: 50px;
  }
}
@media (max-width: 768px) {
  .first__step__content {
    width: 100%;
  }
  .first__step__content p.header {
    width: 100%;
  }
  .first__step__content .btn__default {
    width: 100%;
  }
}
@media (max-width: 604px) {
  .first__step__images {
    height: auto;
  }
}
.last__step {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 650px;
  flex-direction: column;
  padding: 0 30px;
}
.last__step p.title {
  font-size: 36px;
  font-weight: 700;
  line-height: 140%;
  color: #363636;
  text-align: center;
}
.last__step p.text {
  font-weight: 400;
  margin-top: 15px;
  color: rgba(54, 54, 54, 0.7);
  font-size: 18px;
  text-align: center;
}

@media (max-width: 1280px) {
  .last__step {
    height: 100vh;
  }
}
@media (max-width: 990px) {
  .last__step p.title {
    font-size: 24px;
    line-height: 100%;
  }
  .last__step p.text {
    font-size: 16px;
  }
}
.choice__style__wrapp {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  column-gap: 20px;
  row-gap: 15px;
  margin-top: 30px;
}
.choice__style__wrapp .choice__style__item {
  width: calc(50% - 20px);
  border: 1px solid #d9d9d9;
  display: flex;
  border-radius: 5px;
  transition: 0.15s linear;
}
.choice__style__wrapp .choice__style__item.label__active {
  border-color: rgba(211, 64, 133, 0.5);
  transition: 0.3s linear;
}
.choice__style__wrapp .choice__style__item:hover {
  box-shadow: 0 1px 3px 0 #d9d9d9;
  transition: 0.3s linear;
}
.choice__style__wrapp .choice__style__item label.quiz__labels {
  width: 100%;
  height: 100%;
  padding: 15px;
  cursor: pointer;
  display: flex;
  align-items: center;
  position: relative;
}
.choice__style__wrapp .choice__style__item label.quiz__labels input {
  position: relative;
}
.choice__style__wrapp .choice__style__item label.quiz__labels input:after {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  background-color: #d9d9d9;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.choice__style__wrapp .choice__style__item label.quiz__labels input:checked::after {
  border: 2px solid #d34085;
  box-shadow: 0 2px 6px 0 #d34085;
  transition: box-shadow 0.3s ease;
}
.choice__style__wrapp .choice__style__item label.quiz__labels input:checked::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-color: #d34085;
  z-index: 2;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 1s linear;
}
.choice__style__wrapp .choice__style__item label.quiz__labels span {
  color: #363636;
  font-size: 15px;
  margin-left: 15px;
}

@media (max-width: 786px) {
  .choice__style__wrapp {
    flex-direction: column;
    align-items: flex-start;
  }
  .choice__style__wrapp .choice__style__item {
    width: 100%;
  }
}
.choice__loc__option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 30px;
}

.loc__option__item {
  width: 350px;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  transition: 0.3s ease;
}
.loc__option__item.quiz__item__active {
  border-color: rgba(211, 64, 133, 0.5);
  transition: 0.3s ease;
}
.loc__option__item:hover {
  box-shadow: 0 1px 3px 0 #d9d9d9;
  transition: 0.3s ease;
}
.loc__option__item label {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 15;
}
.loc__option__item label input {
  position: absolute;
  right: 30px;
  top: 30px;
  width: 0;
  height: 0;
  transition: 1s ease;
}
.loc__option__item label input:checked::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  background-color: transparent;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 3px solid #d34085;
  box-shadow: 0 2px 6px 0 #d34085;
  transition: box-shadow 0.3s ease;
  transition: 1s ease;
}
.loc__option__item label input:checked::before {
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  background-color: #d34085;
  z-index: 2;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 1s ease;
}
.loc__option__item p.title {
  color: #363636;
  font-size: 15px;
  padding: 10px 15px;
}

.loc__option__images {
  width: 348px;
  height: 219px;
  position: relative;
  z-index: 5;
}
.loc__option__images picture source, .loc__option__images picture img {
  border-radius: 5px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 1122px) {
  .choice__loc__option {
    justify-content: center;
    row-gap: 20px;
    column-gap: 20px;
  }
}
@media (max-width: 794px) {
  .loc__option__item p.title {
    text-align: center;
  }
}
@media (max-width: 512px) {
  .choice__loc__option {
    justify-content: flex-start;
  }
  .loc__option__item {
    width: calc(100% - 15px);
  }
  .loc__option__images {
    width: 100%;
  }
}
.prelast__step {
  padding: 0;
  flex-direction: row;
  overflow: auto;
}
.prelast__step .steps__content {
  height: 100%;
  padding: 0;
  width: 65%;
  justify-content: center;
  padding: 0 75px;
  border-right: 1px solid #d9d9d9;
  overflow: hidden;
}
.prelast__step .steps__content p.title {
  font-size: 26px;
  font-weight: 400;
  color: #363636;
}
.prelast__step .steps__content p.text {
  font-weight: 400;
  color: rgba(54, 54, 54, 0.7);
  font-size: 15px;
  margin-top: 15px;
  line-height: 140%;
}
.prelast__step .steps__form {
  width: 35%;
}

.steps__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 40px;
}
.steps__form .btn__default {
  width: 100%;
  height: 51px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}
.steps__form .btn__default p {
  padding-left: 10px;
}
.steps__form .btn__default svg {
  width: 24px;
  height: 24px;
  fill: white;
}

.steps__form__input__item {
  margin-top: 30px;
}
.steps__form__input__item:first-child {
  margin-top: 0;
}
.steps__form__input__item p.title {
  color: rgba(54, 54, 54, 0.7);
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
}
.steps__form__input__item .steps__form__input {
  margin-top: 10px;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 15px;
}
.steps__form__input__item .steps__form__input .input__icon svg {
  width: 24px;
  height: 24px;
  fill: #d9d9d9;
}
.steps__form__input__item .steps__form__input input {
  width: 100%;
  height: 100%;
  background-color: transparent;
  outline: none;
  padding: 0 10px;
  font-size: 16px;
}
.steps__form__input__item.valid__warning .steps__form__input {
  border-color: #d10a34;
}
.steps__form__input__item.valid__warning .input__valid__warning {
  display: block;
}
.steps__form__input__item .input__valid__warning {
  display: none;
}
.steps__form__input__item .input__valid__warning svg {
  width: 24px;
  height: 24px;
  fill: #d10a34;
}

@media (max-width: 990px) {
  .prelast__step {
    padding: 0;
    flex-direction: column;
  }
  .prelast__step .steps__content {
    height: 50%;
    width: 100%;
    justify-content: center;
    padding: 75px 30px;
    border-right: 1px solid #d9d9d9;
    text-align: center;
  }
  .prelast__step .steps__content p.title {
    font-size: 26px;
    font-weight: 400;
    color: #363636;
  }
  .prelast__step .steps__content p.text {
    font-weight: 400;
    color: rgba(54, 54, 54, 0.7);
    font-size: 15px;
    margin-top: 15px;
    line-height: 140%;
  }
  .prelast__step .steps__form {
    width: 100%;
    height: 50%;
  }
  .steps__form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
}
@media (max-width: 990px) and (min-width: 600px) {
  .steps__content {
    padding: 0 30px;
  }
}